.block--link-list {
  .link-list {
    // display: flex;
    // flex-wrap: wrap;
    // margin: $space-lg 0 0 $space-md;

    @include bp(sm) {
      // margin: $space-lg 0 0 0;
    }
  }

  .link-list--item {
    // position: relative;
    // margin-bottom: $space-md;
    // width: 100%;
    // padding: $space-xs 0;

    @include bp(sm) {
      // width: 50%;
      // padding-right: $space-xs;
    }

    a {
      // display: inline-block;
      font-family: $heading-font;
      // font-weight: 400;
      // line-height: 1.5;
      // padding-left: $space-2xl;
      // color: $link-color;
      // text-decoration: none;
      // transition: 0.3s ease;

      &::before {
        @include vert_center;
        font-family: 'FontAwesome';
        content: '\f15c';
        font-size: $fs-lg;
        left: 0;
        color: inherit;
      }

      // overrides default link underline
      &::after {
        height: 0;
      }

      &:hover,
      &:focus {
        // color: $link-hover-color;

        &::after {
          height: 0;
        }
      }
    }

    /*
 |--------------------------------------------------------------------------
 | Changes icon based on file type
 |--------------------------------------------------------------------------
*/
    $doc-types: ('.com': '\f35d',
      '.net': '\f35d',
      '.edu': '\f35d',
      '.org': '\f35d',
      '.docx': '\f1c2',
      '.pdf': '\f1c1',
      '.xlsx': '\f1c3',
      '.jpg': '\f1c5',
      '.png': '\f1c5',
    );

    @each $docType,
    $icon in $doc-types {
      @if "a[href*='#{$docType}']" {
        a[href*='#{$docType}']::before {
          content: $icon;
        }
      }
    }
  }
}

